@import url(https://fonts.googleapis.com/css?family=Lato|Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* font-family: 'Lato', sans-serif;
font-family: 'Raleway', sans-serif; */

body {
  font-size: 16px;
  width: 80vw;
  margin: auto
}

h1 {
  font-family: 'Lato', sans-serif;
  font-size: 5.5rem;
  font-weight: 300;
  color: #2E2424;
  text-shadow: #A7A7A7 1px 3px 2px;
  margin-top: 0px;
}

h2 {
  font-family: 'Lato', sans-serif;
  font-size: 2.5rem;
  font-weight: 300;
  color: #80C79E;
  margin-bottom: 0px;
}

h3 {
  font-family: 'Lato', sans-serif;
  font-size: 1.5rem;
  font-weight: 500;
  color: #2E2424;
  margin-bottom: 0px;
}
header {
  height: 8vh;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 15vh;
}

#logo {
  flex: 1 1;
  margin-top: 1vh;
}

#logo > h2 {
  color: #2E2424;
  margin: 0px;
  display: block;
  width: 4.5rem;
  text-align: right;
  border-bottom: 0.1rem solid #80C79E;
}

nav {
  flex: 2 1;
  margin-right: 20vw;
  margin-top: 1vh;
}

nav > ul {
  list-style: none;
  list-style-image: none;
  display: flex;
  flex-wrap: wrap;
}

nav > ul > li {
  flex: 1 1;
  display: list-item;
  font-family: 'Lato', sans-serif;
  font-size: 1rem;
  font-weight: 500;
  color: #2E2424;
  margin-bottom: 0px;
}
#main-headline {
  margin-left: 3vw;
}
